import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesOrderComponent } from './sales-order.component';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { Routes, RouterModule } from '@angular/router';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { SalesOrderService } from 'src/app/service/sales-order.service';

const routes: Routes = [
  {
    path: '',
    component: SalesOrderComponent,
    data: { title: 'sales_order_list' },
  },
  {
    path: 'add',
    loadChildren: () => import('./add-sales-order/add-sales-order.module').then(m => m.AddSalesOrderModule),
    data: { title: 'add_sales_order' },
 
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('./add-sales-order/add-sales-order.module').then(m => m.AddSalesOrderModule),
    data: { title: 'edit_sales_order' },
 
  },
  {
    path: 'view/:id',
    loadChildren: () => import('./sales-order-view/sales-order-view.module').then(m => m.SalesOrderViewModule),
    data: { title: 'view_sales_order' },

  }

];



@NgModule({
  declarations: [SalesOrderComponent],
  imports: [
    CommonModule,
    SearchModule,
    BtnAddModule,
    TableListModule,
    PaginationModule,
    RouterModule.forChild(routes)
  ], providers: [SalesOrderService]
})
export class SalesOrderModule { }
