import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { DataService } from 'src/app/service/data.service';
import { TextField, ActionField, TextViewUrlField, DateField, StatusField } from 'src/app/common-module/table-list/list-field-type';
import { SalesOrderService } from 'src/app/service/sales-order.service';

@Component({
  selector: 'app-sales-order',
  templateUrl: './sales-order.component.html'
})
export class SalesOrderComponent implements OnInit {

  PageTitle = "Manage Sales Order"

  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  headerData: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;

  permissionObject: any = null;
  constructor(
    private paginationService: PaginationService,
    private salesOrderService: SalesOrderService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['activity'];
    // });
    this.setHeaderData();
    this.loadform = false;
    this.getObjects();
  }

  setHeaderData() {
    this.headerData.push(

      new TextViewUrlField({
        label: "Sales Order No",
        key: "sales_order_no",
        viewUrl: {
          path: '/outbound/sales-order/view/',
          id: 'sales_order_id'
        }
      }),
      new TextField({
        label: "Transfer Order No",
        key: '',
      }),
      new DateField({
        label: "Order Date",
        key: "sales_order_date",
      }),
      new TextField({
        label: "Client Name",
        key: 'client.label',
      }),

      new DateField({
        label: "Picking Date",
        key: "sales_order_date",
      }),

      

    );
    let canEdit = true;
    let canView = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/outbound/sales-order/edit/',
        id: 'sales_order_id'
      }
    }
    if (canView) {
      actions['view'] = {
        path: '/outbound/sales-order/view/',
        id: 'sales_order_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'sales-order/',
        id: 'sales_order_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }


  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.salesOrderService.getSalesOrderList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success && response.data) {
        this.objectArray = response.data.list;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;

    });
  }

  getPage(page: number) {
    this.currentPage = page;
    // this.currentPage = 1;
    this.getObjects();
  }

  onReloadEvent() {
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  ngAfterViewChecked(): void {

  }

}






